.contact-section {
  display: block;
  overflow: auto;
  min-height: 100vh;

  background: url(../../assets/Contact/pawel-orange-ridge.jpg) no-repeat center bottom fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.contact-link {
  font-family: Poppins-Bold;
  text-decoration: none;
  color: white;

  text-align: justify;
}

.contact-link:hover {
  // color: rgb(80, 160, 252);
  color: rgb(7, 176, 206);
  // text-decoration: underline solid rgb(7, 176, 206) 3.5px;
}

.linkedin-link:after, .instagram-link:after, .github-link:after, .twitter-link:after {
  content: "";
  display: inline-block;
  width: 100%;
}

.email, .phone {
  font-family: Roboto-Medium;
  color: white;
  letter-spacing: 0.3em;
  margin-top: 1px;
}

.contact-container {
  @media screen and (min-height: 2048px) {
    padding-top: 825px;
  }
  
  @media screen and (min-height: 1901px) and (max-height: 2047px) {
    padding-top: 725px;
  }
  
  @media screen and (min-height: 1801px) and (max-height: 1900px) {
    padding-top: 675px;
  }
  
  @media screen and (min-height: 1701px) and (max-height: 1800px) {
    padding-top: 625px;
  }
  
  @media screen and (min-height: 1601px) and (max-height: 1700px) {
    padding-top: 575px;
  }
  
  @media screen and (min-height: 1501px) and (max-height: 1600px) {
    padding-top: 525px;
  }
  
  @media screen and (min-height: 1401px) and (max-height: 1500px) {
    padding-top: 475px;
  }
  
  @media screen and (min-height: 1301px) and (max-height: 1400px) {
    padding-top: 425px;
  }
  
  @media screen and (min-height: 1201px) and (max-height: 1300px) {
    padding-top: 375px;
  }
  
  @media screen and (min-height: 1101px) and (max-height: 1200px) {
    padding-top: 325px;
  }
  
  @media screen and (min-height: 1001px) and (max-height: 1100px) {
    padding-top: 275px;
  }
  
  @media screen and (min-height: 901px) and (max-height: 1000px) {
    padding-top: 250px;
  }
  
  @media screen and (min-height: 801px) and (max-height: 900px) {
    padding-top: 225px;
  }
  
  @media screen and (min-height: 701px) and (max-height: 800px) {
    padding-top: 200px;
  }
  
  @media screen and (min-height: 601px) and (max-height: 700px) {
    padding-top: 175px;
  }
  
  @media screen and (min-height: 501px) and (max-height: 600px) {
    padding-top: 150px;
  }
  
  @media screen and (min-height: 401px) and (max-height: 500px) {
    padding-top: 125px;
  }
  
  @media screen and (max-height: 400px) {
    padding-top: 100px;
  }
}

