.timeline-wrapper {
  min-height: 100vh;
  color: #fff;
  position: relative;
}

.section0,
.section1,
.section2,
.section3,
.section4,
.section5,
.section6,
.section7 {
  background: url(../../assets/Contact/pawel-orange-ridge.jpg) no-repeat center
    center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.section {
  height: 100vh;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.intro-outer-wrapper {
  display: grid;
  width: 54%;
  height: 70%;

  border-radius: 7px;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.8);

  @media screen and (min-width: 2160px) {
    font-family: Roboto-Medium;
    font-size: 20px;
    letter-spacing: 0.15em;
  }

  @media screen and (min-width: 1311px) and (max-width: 2159px) {
    font-family: Roboto-Medium;
    font-size: 19.5px;
    letter-spacing: 0.14em;
  }

  @media screen and (min-width: 768px) and (max-width: 1310px) {
    font-family: Roboto-Medium;
    font-size: 19px;
    letter-spacing: 0.13em;
  }

  @media screen and (min-width: 580px) and (max-width: 767px) {
    font-family: Roboto-Medium;
    font-size: 18.5px;
    letter-spacing: 0.12em;
  }

  @media screen and (min-width: 476px) and (max-width: 579px) {
    font-family: Roboto-Medium;
    font-size: 18px;
    letter-spacing: 0.11em;
  }

  @media screen and (min-width: 414px) and (max-width: 475px) {

    height: 80%;

    font-family: Roboto-Medium;
    font-size: 17px;
    letter-spacing: 0.08em;
  }

  @media screen and (max-width: 413px) {

    height: 85%;

    font-family: Roboto-Medium;
    font-size: 14px;
    letter-spacing: 0.05em;
  }

  .intro-inner-wrapper {
    margin: auto;
  }
}

.info-outer-wrapper {
  display: grid;
  width: 54%;
  height: 70%;

  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.8);

  @media screen and (min-width: 701px) and (max-width: 940px) {
    height: 85%;
  }

  @media screen and (min-width: 414px) and (max-width: 700px) {
    height: 86%;
  }

  @media screen and (max-width: 413px) {
    height: 87%;
  }

  .info-inner-wrapper {
    width: 70%;
    margin: auto;
    padding: 20px;

    @media screen and (min-width: 414px) and (max-width: 475px) {
      width: 95%;
    }
  
    @media screen and (max-width: 413px) {
      width: 100%;
    }
  }
}

.section-subtitle {
  @media screen and (min-width: 2160px) {
    font-family: Roboto-Medium;
    font-size: 17px;
    letter-spacing: 0.15em;
  }

  @media screen and (min-width: 1311px) and (max-width: 2159px) {
    font-family: Roboto-Medium;
    font-size: 17px;
    letter-spacing: 0.15em;
  }

  @media screen and (min-width: 768px) and (max-width: 1310px) {
    font-family: Roboto-Medium;
    font-size: 15px;
    letter-spacing: 0.12em;
  }

  @media screen and (min-width: 580px) and (max-width: 767px) {
    font-family: Roboto-Medium;
    font-size: 13px;
    letter-spacing: 0.09em;
  }

  @media screen and (min-width: 476px) and (max-width: 579px) {
    font-family: Roboto-Medium;
    font-size: 11px;
    letter-spacing: 0.06em;
  }

  @media screen and (min-width: 414px) and (max-width: 475px) {
    font-family: Roboto-Medium;
    font-size: 10.5px;
    letter-spacing: 0.05em;
  }

  @media screen and (max-width: 413px) {
    font-family: Roboto-Medium;
    font-size: 10px;
    letter-spacing: 0.05em;
  }
}

.section-done,
.section-courses,
.section-learned {
  @media screen and (min-width: 2160px) {
    font-family: Roboto Regular;
    font-size: 14px;
    letter-spacing: 0.05em;
  }

  @media screen and (min-width: 1311px) and (max-width: 2159px) {
    font-family: Roboto Regular;
    font-size: 14px;
    letter-spacing: 0.05em;
  }

  @media screen and (min-width: 768px) and (max-width: 1310px) {
    font-family: Roboto Regular;
    font-size: 13px;
    letter-spacing: 0.03em;
  }

  @media screen and (min-width: 580px) and (max-width: 767px) {
    font-family: Roboto Regular;
    font-size: 12px;
    letter-spacing: 0.03em;
  }

  @media screen and (min-width: 476px) and (max-width: 579px) {
    font-family: Roboto Regular;
    font-size: 11px;
    letter-spacing: 0.01em;
  }

  @media screen and (min-width: 414px) and (max-width: 475px) {
    font-family: Roboto Regular;
    font-size: 10px;
    letter-spacing: 0.01em;
  }

  @media screen and (max-width: 413px) {
    font-family: Roboto-Medium;
    font-size: 9px;
    letter-spacing: 0em;
  }
}

.tnav {
  &__wrapper {
    position: fixed;
    height: 100%;
    padding: 0px;

    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
  }

  @media screen and (min-width: 2160px) {
    & {
      margin: 0 0 20px 29px;
    }

    &__date {
      font: normal normal normal 20px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 1311px) and (max-width: 2159px) {
    & {
      margin: 0 0 20px 29px;
    }

    &__date {
      font: normal normal normal 20px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1310px) {
    & {
      margin: 0 0 20px 29px;
    }

    &__date {
      font: normal normal normal 19px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 580px) and (max-width: 767px) {
    & {
      margin: 0 0 20px 15px;
    }

    &__date {
      font: normal normal normal 14px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 415px) and (max-width: 579px) {
    & {
      margin: 0 0 20px 13px;
    }

    &__date {
      font: normal normal normal 12px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (max-width: 414px) {
    & {
      margin: 0 0 20px 11px;
    }

    &__date {
      font: normal normal normal 12px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }
}

