  position: relative;
  transition: all 0.3s ease-out;
  text-decoration: none;
  list-style: none;

  @media screen and (min-height: 2048px) {
    margin-bottom: 11.7em;
  }

  @media screen and (min-height: 1901px) and (max-height: 2047px) {
