.banner-image {
  min-height: 100vh;
  background: url(../../assets/Home/pawel-orange-ridge.jpg) no-repeat center top fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.layer {
  display: grid;
  height: 100%;
  width: 100%;

  background-color: rgba(0, 0, 0, 0.4);

  .home-container {
    margin: auto;
  }
}

.blinking-cursor:after {
  content: "|";
  text-shadow: 0 0 30px rgb(0, 0, 0);
  animation: blink 1.2s step-start infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.home-intro {
  padding-bottom: 7px;
}

.home-intro::before {
  content: "";
  display: inline-block;
  border-top: 2px solid rgb(255, 205, 111);
  height: 9px;
  width: 35px;
}

.home-intro-space {
  content: "";
  display: inline-block;
  width: 17.5px;
}

@media only screen and (min-width: 2160px) {
  .home-intro {
    letter-spacing: 0.25em;
    color: rgb(255, 205, 111);
    text-shadow: 0 0 15px rgb(0, 0, 0);
    font: normal normal normal 21px/1.4em Roboto Regular;
  }

  .home-text {
    line-height: 5.4rem;
    font: normal normal normal 105.5px/1.4em PFD-SB;
    text-shadow: 0 0 30px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }
}

