.navbar {
  background-color: transparent !important;
  // background-color: rgba(0, 0, 0, 0.95) !important;
  // background-color: rgba(255, 167, 5, 0.99) !important;
  justify-content: space-around;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
}

.hlink {
  text-decoration: none;
}

.navbar-brand {
  font: normal normal normal 20px/1.4em Poppins Semibold !important;
}

.nav-item {
  font: normal normal normal 16px/1.4em Poppins Regular !important;
}

.link {
  text-decoration: none;
}

.social-icon {
  font-size: 1.1rem !important;
  margin-right: 2px;
}

.navbar-icons {
  padding-right: 20px;
}

.nav-link:hover {
  color: white !important;
}

.header-nav-active {
  color: white !important;
}

@media only screen and (max-width: 991px) {
  .navbar-icons {
      display: none !important;
  }

  .navbar-collapse {
      background-color: rgba(25, 25, 25, 0.75) !important;
      width: 100% !important;
  }

  .navbar-collapse .collapse {
    display: none !important;
    width: 100vw !important;
  }
}