.about-title, .about-text, .orange-text {
  color: white;
}

@media only screen and (min-width: 2160px) {
  .about-container {
    margin-top: 17%;
    scale: 1.3;
  }

  .about-title {
    font: normal normal normal 30px/35px Poppins-Bold;
    text-shadow: 0 0 5px rgba(0, 0, 0, 1);
  }

  .about-text {
    font: normal normal normal 26px/35px Roboto Regular;
    text-shadow: 0 0 5px rgba(0, 0, 0, 1);
  }
}

@media only screen and (min-width: 1441px) and (max-width: 2159px) {
  .about-container {
    margin-top: 270px;
    scale: 0.95;
  }

  .about-title {
    /* font: normal normal normal 37px/40px Poppins-Bold; */
    font: normal normal normal 29px/30px Poppins-Bold;
    text-shadow: 0 0 10px rgba(0, 0, 0, 1);
    margin-bottom: 20px;
  }

  .about-text {
    letter-spacing: 0.008em;
    /* font: normal normal normal 27px/43px Roboto-Medium; */
    font: normal normal normal 21px/30px Roboto-Medium;
    text-shadow: 0 0 10px rgba(0, 0, 0, 1);
  }
}

@media only screen and (min-width: 1311px) and (max-width: 1440px) {
  .about-container {
    margin-top: 140px;
    scale: 0.95;
  }

  .about-title {
    /* font: normal normal normal 37px/40px Poppins-Bold; */
    font: normal normal normal 29px/30px Poppins-Bold;
    margin-bottom: 20px;
  }

  .about-text {
    letter-spacing: 0.008em;
    /* font: normal normal normal 27px/43px Roboto-Medium; */
    font: normal normal normal 21px/30px Roboto-Medium;
  }
}

@media only screen and (min-width: 1001px) and (max-width: 1310px) {
  .about-container {
    margin-top: 90px;
    scale: 0.85;
  }

  .about-title {
    font: normal normal normal 27px/28px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }

  .about-text {
    font: normal normal normal 19px/28px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }
}

@media only screen and (min-width: 768px) and (max-width: 1000px) {
  .about-container {
    margin-top: 40px;
    scale: 0.85;
  }

  .about-title {
    font: normal normal normal 27px/28px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }

  .about-text {
    font: normal normal normal 19px/28px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }
}

@media only screen and (min-width: 580px) and (max-width: 767px) {
  .about-container {
    margin-top: 15px;
    scale: 0.85;
  }

  .about-title {
    font: normal normal normal 25px/26px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }

  .about-text {
    font: normal normal normal 17px/26px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }
}

@media only screen and (min-width: 415px) and (max-width: 579px) {
  .about-container {
    margin-top: 5px;
    scale: 0.8;
  }

  .about-title {
    font: normal normal normal 22px/24px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }

  .about-text {
    font: normal normal normal 14px/24px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }
}

@media only screen and (max-width: 414px) {
  .about-container {
    margin-top: 0px;
    scale: 0.8;
  }

  .about-title {
    font: normal normal normal 22px/24px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }

  .about-text {
    font: normal normal normal 14px/24px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  }
}





