.vnav-wrapper {
  min-height: 100vh;
  position: fixed;
  display: flex;
  top: 0;
  right: 0;
  justify-content: flex-end;
}

@media only screen and (min-width: 811px) {
  .vnav-wrapper {
    padding-right: 50px;
  }
}

@media only screen and (min-width: 415px) and (max-width: 810px) {
  .vnav-wrapper {
    padding-right: 25px;
  }
}

@media only screen and (max-width: 414px) {
  .vnav-wrapper {
    padding-right: 5px;
  }
}

.vnav-wrapper {
  @media screen and (min-height: 2048px) {
    padding-top: 875px;
  }
  
  @media screen and (min-height: 1901px) and (max-height: 2047px) {
    padding-top: 775px;
  }
  
  @media screen and (min-height: 1801px) and (max-height: 1900px) {
    padding-top: 725px;
  }
  
  @media screen and (min-height: 1701px) and (max-height: 1800px) {
    padding-top: 675px;
  }
  
  @media screen and (min-height: 1601px) and (max-height: 1700px) {
    padding-top: 625px;
  }
  
  @media screen and (min-height: 1501px) and (max-height: 1600px) {
    padding-top: 575px;
  }
  
  @media screen and (min-height: 1401px) and (max-height: 1500px) {
    padding-top: 525px;
  }
  
  @media screen and (min-height: 1301px) and (max-height: 1400px) {
    padding-top: 475px;
  }
  
  @media screen and (min-height: 1201px) and (max-height: 1300px) {
    padding-top: 425px;
  }
  
  @media screen and (min-height: 1101px) and (max-height: 1200px) {
    padding-top: 375px;
  }
  
  @media screen and (min-height: 1001px) and (max-height: 1100px) {
    padding-top: 325px;
  }
  
  @media screen and (min-height: 901px) and (max-height: 1000px) {
    padding-top: 275px;
  }
  
  @media screen and (min-height: 801px) and (max-height: 900px) {
    padding-top: 225px;
  }
  
  @media screen and (min-height: 701px) and (max-height: 800px) {
    padding-top: 175px;
  }
  
  @media screen and (min-height: 601px) and (max-height: 700px) {
    padding-top: 125px;
  }
  
  @media screen and (min-height: 501px) and (max-height: 600px) {
    padding-top: 75px;
  }
  
  @media screen and (min-height: 401px) and (max-height: 500px) {
    padding-top: 50px;
  }
  
  @media screen and (max-height: 400px) {
    padding-top: 25px;
  }
}


.vnav {
  writing-mode: vertical-lr;
  -webkit-writing-mode: vertical-lr;
  -ms-writing-mode: vertical-lr;
  text-orientation: sideways;
  transform: rotate(-180deg);

  &__wrapper {
    position: fixed;
    height: 100%;
    width: 0%;
    background: transparent;

    display: flex;
    flex-flow: column nowrap;
  }
}

.vnav-li {
  position: relative;
  transition: all 0.3s ease-out;
  margin-bottom: 20px;
  text-decoration: none;
  list-style: none;
  display: inline-block;
}

.vnav-a {
  display: block;
  padding: 0;
  color: white;
  transition: all 0.15s ease-out;
  text-decoration: none;
  letter-spacing: 0.22em;
}

@media only screen and (min-width: 415px) {
  .vnav-a {
    font: normal normal normal 14px/1.4em Roboto-Medium !important;
  }
}

@media only screen and (max-width: 414px) {
  .vnav-a {
    font: normal normal normal 10.7px/1.4em Roboto-Medium !important;
  }
}

.vnav-a:hover {
  // color: rgb(255, 205, 111);
  color: rgb(255, 167, 5);
}